import { createContext, useState, useEffect } from "react";

export const CalendarContext = createContext({});

export const useReminders = ({ selectedDate }) => {
  // If there's data in local storage here i bring it, if not just initializes reminders to an empty array
  const remindersInLocalStorage = window.localStorage.getItem("reminders");
  const initialState = remindersInLocalStorage
    ? JSON.parse(remindersInLocalStorage)
    : [];

  const [reminders, setReminders] = useState(initialState);

  const [selectedReminder, setSelectedReminder] = useState(null);

  useEffect(() => {
    window.localStorage.setItem("reminders", JSON.stringify(reminders));
  }, [reminders]);

  const addReminder = ({ title, time, city }) => {
    const id = Date.now();
    setReminders((reminders) => [
      ...reminders,
      { id, title, date: selectedDate, time, city }
    ]);
  };

  const getReminders = (date = selectedDate) => {
    const remindersForDate = reminders.filter(
      (reminder) => reminder.date === date
    );
    return remindersForDate;
  };

  const getReminder = () => {
    const reminder = reminders.find(
      (reminder) => reminder.id === selectedReminder
    );
    return reminder;
  };

  const editReminder = (reminderToEdit) => {
    setReminders((reminders) =>
      reminders.map((reminder) =>
        reminder.id === reminderToEdit.id ? reminderToEdit : reminder
      )
    );
  };

  const deleteReminder = () => {
    console.log({ selectedReminder });
    const remainingReminders = reminders.filter(
      (reminder) => reminder.id !== selectedReminder
    );
    return setReminders(remainingReminders);
  };

  return {
    reminders,
    setSelectedReminder,
    addReminder,
    editReminder,
    getReminders,
    getReminder,
    selectedReminder,
    deleteReminder
  };
};
