import { useState } from "react";

export const useModal = ({ setSelectedDate, setSelectedReminder }) => {
  const [modalStatus, setModalStatus] = useState(null);

  const openModal = (date, modalType, id) => {
    setSelectedDate(date);
    setModalStatus(modalType);
    id && setSelectedReminder(id);
  };

  const closeModal = () => setModalStatus(null);

  return {
    modalStatus,
    openModal,
    closeModal
  };
};
