import { createContext, useState } from "react";
import { useReminders, useModal } from "src/hooks";

export const CalendarContext = createContext({});

export const CalendarProvider = ({ children }) => {
  const [selectedDate, setSelectedDate] = useState(null);

  const {
    reminders,
    setSelectedReminder,
    addReminder,
    editReminder,
    getReminders,
    getReminder,
    selectedReminder,
    deleteReminder
  } = useReminders({ selectedDate });

  const { modalStatus, openModal, closeModal } = useModal({
    setSelectedDate,
    setSelectedReminder
  });

  return (
    <CalendarContext.Provider
      value={{
        reminders,
        addReminder,
        editReminder,
        modalStatus,
        openModal,
        closeModal,
        selectedDate,
        getReminders,
        getReminder,
        selectedReminder,
        deleteReminder
      }}
    >
      {children}
    </CalendarContext.Provider>
  );
};
